
















import Vue from "vue";

export default Vue.extend({
  name: "Show",
  data() {
    return {
      content: "",
      snackbar: false,
    };
  },
  created() {
    this.reqText();
  },
  methods: {
    async reqText() {
      const { v } = this.$route.query;
      if (Array.isArray(v)) return;
      try {
        const res = await this.$http.getText(v);
        if (res.code !== 0) {
          console.log(res.msg);
          throw "发生了错误";
        }
        this.content = res.data;
        console.log(res.data)
      } catch (error) {
        this.snackbar = true;
      }
    },
  },
});
